@import "../neat/neat-helpers";

// Neat Overrides
$column: percentage(1/12);
$gutter: 0%;
$grid-columns: 12;
$max-width: percentage(12/12);

$visual-grid: false;
$visual-grid-color: $salmon;
$visual-grid-index: front;
$visual-grid-opacity: 0.5;

// Neat Breakpoints
$medium-screen: 768px;
$large-screen: 1024px;
$xlarge-screen: 1440px;

$medium-screen-up: new-breakpoint(min-width $medium-screen 12);
$large-screen-up: new-breakpoint(min-width $large-screen 12);
$xlarge-screen-up: new-breakpoint(min-width $xlarge-screen 12);
